.borderStyleOfThAndTd,
.weldingModificationFormOfReading th,
.weldingModificationFormOfReading td,
.weldingCheckFormPublicComponent th {
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
}
.WeldingTechDetailForm {
  min-width: 100%;
  background-color: #fff;
}
.WeldingTechDetailForm th {
  white-space: pre-line;
  min-width: 2.6rem;
  font-size: .9rem;
  text-align: center;
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
}
.WeldingTechDetailForm td {
  text-align: center;
  border-bottom: 1px solid #000;
  font-size: .9rem;
  font-weight: 600;
  border-right: 1px solid #000;
  height: 1.8rem;
}
.WeldingTechDetailForm tr:nth-child(even) {
  background-color: #e4e4e4;
}
.WeldingTechDetailForm td:last-child {
  border-right: 0;
  min-width: 8rem;
  max-width: 8rem;
}
.WeldingTechDetailForm th:last-child {
  border-right: 0;
}
.two-level-title {
  font-weight: 800!important;
}
.whiteRow {
  background-color: #fff!important;
}
.weldingModificationFormOfReading {
  width: 100%;
  border-collapse: collapse;
  font-size: 1rem;
  font-weight: 600;
  table-layout: fixed;
}
.weldingModificationFormOfReading th {
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  min-width: 2rem;
}
.weldingModificationFormOfReading th:first-child {
  border-left: 1px solid black;
  min-width: 5rem;
}
.weldingModificationFormOfReading .first-th {
  min-width: 100%;
}
.weldingModificationFormOfReading td {
  min-width: 1.8rem;
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  font-weight: 700;
}
.weldingModificationFormOfReading td input {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  text-align: center;
  font-weight: 700;
}
.weldingModificationFormOfReading td textarea {
  font-weight: 700;
}
.weldingModificationFormOfReading td:first-child {
  border-left: 1px solid black;
}
.weldingModificationFormOfReading td:nth-of-type(n+3) {
  max-width: 1.8rem;
}
.weldingCheckFormPublicComponent {
  width: 100%;
}
.weldingCheckFormPublicComponent th {
  height: 2rem;
  line-height: 2rem;
  text-align: center;
  min-width: 2rem;
}
.weldingCheckFormPublicComponent td {
  text-align: center;
  border-bottom: 1px solid #000;
  font-size: .9rem;
  font-weight: 600;
  border-right: 1px solid #000;
  height: 1.8rem;
}
.weldingCheckFormPublicComponent .narrowUnitStyleInWeldingCheckForm {
  min-width: 1.4rem;
  max-width: 1.4rem;
  line-height: 1rem;
}
.weldingCheckFormPublicComponent td:first-child {
  border-left: 1px solid black;
}
.weldingCheckFormPublicComponent th:first-child {
  border-left: 1px solid black;
  min-width: 5rem;
}
.weldingCheckFormPublicComponent tr:first-child th {
  border-top: 1px solid black;
}



