.login {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  background: #f3f3f3;
}
.login .login-form {
  width: 320px;
  padding: 36px;
  -webkit-box-shadow: 0 0 100px rgba(0, 0, 0, 0.08);
          box-shadow: 0 0 100px rgba(0, 0, 0, 0.08);
  background: #fff;
}
.login .login-form .login-logo {
  width: 100%;
  height: 40px;
  background: url('img/sun.png') no-repeat 10px center / 15%;
  margin-bottom: 20px;
}
.login .login-form .login-logo .login-name {
  line-height: 40px;
  text-align: center;
  padding-left: 15px;
  font-size: 20px;
}
.login .login-form .githubUrl {
  display: block;
  margin: 0 auto;
  width: 25px;
  height: 25px;
  background: url('img/github.png') no-repeat center center / cover;
}
.loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
}



