.publicBorderStyle {
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
}
.GluingTechDetailForm {
  width: 100%;
  background-color: #fff;
  text-align: center;
}
.GluingTechDetailForm th {
  font-size: .9rem;
  min-width: 2.6rem;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
}
.GluingTechDetailForm td {
  text-align: center;
  font-size: .9rem;
  font-weight: 600;
  height: 1.8rem;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
}
.GluingTechDetailForm th:last-child {
  border-right: 0;
}
.GluingTechDetailForm td:last-child {
  border-right: 0;
}
.GluingTechDetailForm tr:nth-child(even) {
  background-color: #e4e4e4;
}
.two-level-title {
  font-weight: 800!important;
}
.whiteRow {
  background-color: #fff!important;
}
.ModificationFormOfReading {
  width: 100%;
  font-size: 1rem;
  font-weight: 600;
}
.ModificationFormOfReading .tableTitle {
  border: 1px solid black;
  border-top: none;
}
.ModificationFormOfReading th {
  height: 2rem;
  line-height: 2rem;
  text-align: center;
}
.ModificationFormOfReading td {
  width: 25%;
  text-align: center;
  height: 2rem;
  line-height: 2rem;
  border: 1px solid black;
  border-top: none;
}
.ModificationFormOfReading .currentParamsTd {
  background-color: #e8e8e8;
}
.ModificationFormOfReading input {
  height: 2rem;
  line-height: 2rem;
  width: 100%;
  border: none;
  outline: none;
  text-align: center;
}



