.weldingParamsView {
  width: 100%;
  min-height: 300px;
}
.weldingParamsView .leftOfWeldingParamsView {
  float: left;
  height: calc(100vh - 207px - 3.2rem);
  overflow-y: auto;
  border-right: 2px solid rgba(0, 0, 0, 0.1);
}
.weldingParamsView .leftOfWeldingParamsView #components-back-top-demo-custom .ant-back-top {
  bottom: 100px;
}
.weldingParamsView .leftOfWeldingParamsView #components-back-top-demo-custom .ant-back-top-inner {
  height: 40px;
  width: 40px;
  line-height: 40px;
  border-radius: 4px;
  background-color: #1088e9;
  color: #fff;
  text-align: center;
  font-size: 20px;
}
.weldingParamsView .leftOfWeldingParamsView::-webkit-scrollbar {
  display: none;
}
.weldingParamsView .rightOfWeldingParamsView {
  float: right;
  width: calc(100% - 259px);
  min-height: 100px;
}
.weldingParamsView .rightOfWeldingParamsView .workshopTitle {
  margin-left: 6%;
  font-size: 1rem;
  font-weight: 900;
  margin-bottom: .4rem;
}
.weldingParamsView .rightOfWeldingParamsView .workshopTitle .workshopTitleItem {
  display: inline-block;
  padding-right: 1rem;
  min-width: 6rem;
}
.weldingParamsView .rightOfWeldingParamsView .searchBlock {
  width: 84%;
  margin-left: 6%;
  border-radius: 12px;
  background-color: rgba(211, 211, 211, 0.3);
  padding-top: .4rem;
  padding-bottom: .4rem;
}
.weldingParamsView .rightOfWeldingParamsView .searchBlock .searchRow {
  width: 90%;
  margin: 4px auto 4px;
}
.weldingParamsView .rightOfWeldingParamsView .searchBlock .searchRow .searchItem {
  display: inline-block;
  width: 24%;
  margin-right: 1rem;
}
.weldingParamsView .rightOfWeldingParamsView .searchBlock .searchRow .searchItem span {
  font-size: .9rem;
  font-weight: 700;
}
.weldingParamsView .rightOfWeldingParamsView .searchBlock .searchRow div:last-child {
  margin-right: 0;
}
.weldingParamsView .rightOfWeldingParamsView .searchBlock .checkboxBlock {
  margin-bottom: .8rem;
  font-size: .9rem;
  font-weight: 700;
}
.weldingParamsView .rightOfWeldingParamsView .tableComponent {
  margin-left: 6%;
  margin-top: 1rem;
  width: 84%;
}
.weldingParamsDetailsView {
  margin-top: 48px;
}
.weldingParamsDetailsView .pageHeader {
  border: 1px solid #ebedf0;
  width: calc(100% + 4px);
  margin-left: -2px;
}
.weldingParamsDetailsView .weldingParamsDetailsViewContent {
  margin-top: 1rem;
  overflow-x: auto;
  margin-bottom: 1rem;
}
.weldingParamsDetailsView .weldingParamsDetailsViewContent .titleBlock {
  width: 92%;
  height: 5rem;
  background-color: #e4e4e4;
  margin: 0 auto 1rem;
  border-radius: 8px;
}
.weldingParamsDetailsView .weldingParamsDetailsViewContent .titleBlock .firstTitleRow {
  width: 96%;
  margin: 0 auto;
  height: 2.5rem;
}
.weldingParamsDetailsView .weldingParamsDetailsViewContent .titleBlock .firstTitleRow span {
  display: inline-block;
  height: 2.5rem;
  font-size: 1rem;
  padding-top: .8rem;
  font-weight: 700;
  margin-right: 1.8rem;
}
.weldingParamsDetailsView .weldingParamsDetailsViewContent .titleBlock .secondTitleRow {
  width: 96%;
  margin: 0 auto;
  height: 2.5rem;
}
.weldingParamsDetailsView .weldingParamsDetailsViewContent .titleBlock .secondTitleRow span {
  display: inline-block;
  padding-top: .2rem;
  height: 2.5rem;
  font-size: 1rem;
  font-weight: 700;
  margin-right: 1.8rem;
}
.loadingStyle {
  text-align: center;
  margin-top: 5rem;
}



