.gluingParamsView {
  margin-top: 48px;
}
.gluingParamsDetailView {
  margin-top: 48px;
}
.gluingParamsDetailView .pageHeader {
  border: 1px solid #ebedf0;
  width: calc(100% + 4px);
  margin-left: -2px;
}
.gluingParamsDetailView .titleBlock {
  width: 92%;
  background-color: #e4e4e4;
  margin: 1rem auto 1rem;
  border-radius: 8px;
  padding-top: .5rem;
  padding-bottom: .5rem;
}
.gluingParamsDetailView .titleBlock .rowOfTitleBlock {
  width: 96%;
  margin: 0 auto;
  line-height: 2.3rem;
  font-weight: 700;
  font-size: 1rem;
}
.gluingParamsDetailView .titleBlock .rowOfTitleBlock span {
  margin-right: 2rem;
}



