.putIssue {
  width: 100%;
}
.putIssue .putIssueForm {
  display: block;
  margin: 2rem 0 2rem;
}
.putIssue .leftOfPutIssue {
  float: left;
  width: 40%;
}
.putIssue .leftOfPutIssue .secondItemInPutIssue {
  display: inline-block;
  float: right;
  width: 34%;
}
.putIssue .leftOfPutIssue .secondItemInPutIssue .inputInSecondItemInPutIssue {
  width: 100%!important;
  margin-bottom: 10px!important;
}
.putIssue .leftOfPutIssue .secondItemInPutIssue .inputInSecondItemInPutIssue:first-child {
  margin-top: 5px!important;
}
.putIssue .leftOfPutIssue .secondItemInPutIssue .inputInSecondItemInPutIssue:last-child {
  margin-bottom: 0;
}
.putIssue .rightOfPutIssue {
  float: left;
  width: 60%;
}
.putIssue .rightOfPutIssue .secondItemInPutIssue {
  display: inline-block;
  float: right;
  width: 24%;
  margin-right: 52%;
}
.putIssue .rightOfPutIssue .secondItemInPutIssue .inputInSecondItemInPutIssue {
  width: 100%!important;
  margin-bottom: 10px!important;
  display: block;
}
.putIssue .rightOfPutIssue .secondItemInPutIssue .inputInSecondItemInPutIssue:first-child {
  margin-top: 5px!important;
}
.putIssue .firstItemInPutIssue {
  float: left;
  width: 60%;
}
.putIssue .firstItemInPutIssue div {
  position: relative;
  height: 42px;
}
.putIssue .firstItemInPutIssue div span {
  position: absolute;
  right: 0;
  height: 42px;
  line-height: 42px;
  font-size: 1rem;
  font-weight: 600;
}
.putIssue .changeStyle {
  width: 20%;
}
.putIssue .clear {
  clear: both;
}



