}
.public-table td {
  text-align: center;
  height: 36px;
}
.public-pagination {
  margin-top: 20px;
}
.uploadImage {
  width: 8rem;
  height: 8rem;
  margin: 0 auto;
  border: 1px dashed #d7d7d7;
  border-radius: 1rem;
}
.uploadImage i {
  padding-top: .4rem;
  height: 8rem;
  line-height: 8rem;
  font-size: 6rem;
  color: #e4e4e4;
}
.rectificationDiv {
  position: relative;
  height: 12rem;
}
.rectificationDiv img {
  height: 12rem;
}
.rectificationDiv i {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateY(-50%) translateX(-50%);
      -ms-transform: translateY(-50%) translateX(-50%);
          transform: translateY(-50%) translateX(-50%);
  font-size: 2rem;
